@import url(https://fonts.googleapis.com/css?family=Charmonman|Bungee);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1em;
}

body::-webkit-scrollbar {
  background-color: #212324;
}

body::-webkit-scrollbar-thumb {
  background-color: #464b4d;
}

body.light::-webkit-scrollbar {
  background-color: #f1f1f1;
}

body.light::-webkit-scrollbar-thumb {
  background-color: #c1c1c1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  background-color: #191a1b;
  color: cornsilk;
}

body.light {
  background-color: white;
  color: #191a1b;
}

main {
  padding: 16px 60px 112px;
  margin-bottom: 50px;
}

#content {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 24px;
  gap: 24px;
}

#return-to-the-top {
  display: block;
  position: fixed;
  bottom: 64px;
  right: 40px;
  height: 56px;
  width: 56px;
  z-index: 2;
  background: none;
  border: none;
  border-radius: 50%;
  box-shadow: 0px 0px 6px 1px #7a7373;
  outline: none;
}

#return-to-the-top > * {
  height: 100%;
  width: 100%;
  color: cornsilk;
}

.light #return-to-the-top > * {
  color: #383a3d;
}

.card {
  display: grid;
  grid-template-rows: 56px auto;
  border: 1px solid #3b3f41;
  border-radius: 8px;
}

.light .card {
  border-color: #dadce0;
}

.font-info {
  margin: 8px 16px 8px;
}

.font-name {
  display: inline;
  font-size: 18px;
  font-weight: 400;
  margin: 0;
}

.sample-text {
  white-space: pre-line;
  padding: 0 16px;
  margin-bottom: 30px;
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 32px;
  text-align: center;
  padding: 0 25px;
  border-top: 1px solid #3b3f41;
  background-color: #1e1e20;
  color: grey;
}

footer > p {
  margin-top: -5px;
}

#portfolio {
  color: cornsilk;
}

a {
  color: inherit;
  text-decoration: none;
  font-family: Bungee;
}

li {
  list-style: none;
}

#header-nav {
  display: flex;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-content: center;
  border-bottom: 1px solid #3b3f41;
  margin-bottom: 1rem;
}

.light #header-nav {
  border-bottom-color: #dadce0;
}

#header-nav > div {
  margin: 0 5%;
}

#header-nav > nav {
  margin-left: 5%;
}

#links > li {
  display: inline-block;
  margin: auto 15px;
}

#header-nav > div > h1 {
  margin: 0;
  font-family: Bungee;
}

#of-all {
  font-family: Charmonman;
  font-weight: 200;
  padding-right: 5px;
}

label {
  display: none;
}

#major-nav {
  display: flex;
  justify-content: center;
  align-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 54px;
  margin: auto 0 20px;
  border-radius: 2rem;
  background-color: #191a1b;
  border: 1px solid #3b3f41;
}

.light #major-nav {
  background-color: white;
  border: 1px solid #dadce0;
}

#major-nav *:focus {
  outline: none;
}

svg#searchIcon {
  margin: auto -16px auto auto;
}

#search,
#custom-text,
#font-sizes,
#list-mode,
#reset {
  margin: auto;
  color: #c3bcb2;
  background-color: #191a1b;
  border-color: transparent;
}

.light #search,
.light #custom-text,
.light #font-sizes,
.light #list-mode,
.light #reset {
  color: inherit;
  background-color: transparent;
}

#search,
#custom-text {
  height: 36px;
  width: 30%;
  margin-right: 10px;
  font-size: 16px;
}

.divider {
  height: 100%;
  display: block;
  border-right: 1px solid #3b3f41;
}

.light .divider {
  border-color: #dadce0;
}

#font-sizes {
  width: 7%;
}

#darkmode-setting {
  margin: auto;
}

#darkmode-label {
  display: inline;
  padding: 6px;
  cursor: pointer;
}

#darkmode-checkbox {
  display: none;
}

#search::-webkit-input-placeholder, #custom-text::-webkit-input-placeholder {
  color: #c3bcb2;
}

#search:-ms-input-placeholder, #custom-text:-ms-input-placeholder {
  color: #c3bcb2;
}

#search::-ms-input-placeholder, #custom-text::-ms-input-placeholder {
  color: #c3bcb2;
}

#search::placeholder,
#custom-text::placeholder {
  color: #c3bcb2;
}

.light #search::-webkit-input-placeholder, .light #custom-text::-webkit-input-placeholder {
  color: #3c4043;
}

.light #search:-ms-input-placeholder, .light #custom-text:-ms-input-placeholder {
  color: #3c4043;
}

.light #search::-ms-input-placeholder, .light #custom-text::-ms-input-placeholder {
  color: #3c4043;
}

.light #search::placeholder,
.light #custom-text::placeholder {
  color: #3c4043;
}

#filter-wrapper {
  margin: auto 0 30px;
}

#filter-wrapper label {
  display: inline;
  cursor: pointer;
}

